.service-navigation {
  background: white;
  box-shadow: 0 0 0 rgba(153, 153, 153, 0.2);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  transform: translateX(-240px);
  transition: box-shadow .25s ease, transform .25s ease;
  width: 240px;
  z-index: 999; }
  .service-navigation_open {
    box-shadow: 4px 0 8px rgba(153, 153, 153, 0.2);
    transform: translateX(0); }
  .service-navigation__inner {
    max-height: 100%;
    overflow: hidden;
    overflow-y: auto;
    padding: 0 0 16px; }
  .service-navigation__list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .service-navigation__item + .service-navigation__item {
    box-shadow: inset 0 1px 0 #efefef; }
  .service-navigation__title {
    background: #009eff;
    color: white;
    display: block;
    font-size: 10px;
    font-weight: 700;
    letter-spacing: .25px;
    line-height: 12px;
    padding: 16px 24px 4px 8px;
    text-transform: uppercase; }
  .service-navigation__item + .service-navigation__title {
    border-top: 1px solid black; }
  .service-navigation__link {
    color: black;
    cursor: pointer;
    display: block;
    font-size: 14px;
    letter-spacing: .25px;
    line-height: 16px;
    padding: 8px 24px 8px 8px;
    position: relative;
    text-decoration: none;
    transition: background .25s ease; }
    .service-navigation__link:hover {
      background: rgba(0, 0, 0, 0.025); }
    .service-navigation__link_child {
      padding: 8px 24px 8px 24px; }
  .service-navigation__link[disabled] {
    color: #aaa;
    pointer-events: none; }
  .service-navigation__trigger {
    align-items: center;
    background: #009eff;
    border: none;
    border-radius: 0 16px 16px 0;
    box-shadow: 0 4px 8px rgba(153, 153, 153, 0.2);
    cursor: pointer;
    display: flex;
    justify-content: center;
    height: 40px;
    outline: none;
    position: absolute;
    right: -40px;
    top: 50vh;
    transition: background .25s ease;
    width: 40px;
    z-index: 2; }
    .service-navigation__trigger:hover {
      background: #0063b2; }
  .service-navigation__icon {
    display: block;
    fill: white;
    flex: 0 0 auto;
    height: 24px;
    width: 24px; }
    .service-navigation_open .service-navigation__icon {
      transform: rotate(-180deg); }
